// import positionVert from './shaders/position.vert.wgsl?raw';
// import colorFrag from './shaders/color.frag.wgsl';

import * as triangle from './triangle';

const positionVert = `
@vertex
fn main(@location(0) position : vec3<f32>) -> @builtin(position) vec4<f32> {
    return vec4<f32>(position, 1.0);
}
`
const colorFrag = `
@group(0) @binding(0) var<uniform> color : vec4<f32>;

@fragment
fn main() -> @location(0) vec4<f32> {
    return color;
}
`

export class Solution {
    divHost: HTMLElement;
    canvas: HTMLCanvasElement;
    ctx: GPUCanvasContext;
    device: GPUDevice;
    format: GPUTextureFormat;
    size: any;
    pipelineObj: any;
    constructor(id: string) {
        this.divHost = document.getElementById(id);
        
    //     console.log(id, 'webGPU', navigator.gpu);
        this.canvas = document.createElement('canvas');
        this.divHost.appendChild(this.canvas);

        this.initWebGPU();
    }
    async initWebGPU() {
        if (!navigator.gpu) { throw new Error('Not Support WebGPU'); }

        const adapter = await navigator.gpu.requestAdapter({
            powerPreference: 'high-performance'
            // powerPreference: 'low-power'
        })

        if (!adapter) { throw new Error('No Adapter Found'); }

        const device = await adapter.requestDevice()
        this.ctx = this.canvas.getContext('webgpu') as GPUCanvasContext
        //@ts-ignore
        const format = navigator.gpu.getPreferredCanvasFormat ? navigator.gpu.getPreferredCanvasFormat() : this.ctx.getPreferredFormat(adapter)
        const devicePixelRatio = window.devicePixelRatio || 1
        this.canvas.width = this.divHost.clientWidth ;
        this.canvas.height = this.divHost.clientHeight;
        this.ctx.configure({
            // json specific format when key and value are the same
            device, format,
            // prevent chrome warning
            alphaMode: 'opaque'
        })

        this.size = { width: this.canvas.width, height: this.canvas.height }
        this.device = device;
        this.format = format;

        this.initPipeline(this.device, this.format);
    }
    public destroy() {
        this.divHost.removeChild(this.canvas);
    }
    async initPipeline(device: GPUDevice, format: GPUTextureFormat) {
        const pipeline = await device.createRenderPipelineAsync({
            label: 'Basic Pipline',
            layout: 'auto',
            vertex: {
                module: device.createShaderModule({
                    code: positionVert,
                }),
                entryPoint: 'main',
                buffers: [{
                    arrayStride: 3 * 4, // 3 float32,
                    attributes: [
                        {
                            // position xyz
                            shaderLocation: 0,
                            offset: 0,
                            format: 'float32x3',
                        }
                    ]
                }]
            },
            fragment: {
                module: device.createShaderModule({
                    code: colorFrag,
                }),
                entryPoint: 'main',
                targets: [
                    {
                        format: format
                    }
                ]
            },
            primitive: {
                topology: 'triangle-list' // try point-list, line-list, line-strip, triangle-strip?
            }
        } as GPURenderPipelineDescriptor)
        // create vertex buffer
        const vertexBuffer = device.createBuffer({
            label: 'GPUBuffer store vertex',
            size: triangle.vertex.byteLength,
            usage: GPUBufferUsage.VERTEX | GPUBufferUsage.COPY_DST,
            //mappedAtCreation: true
        })
        device.queue.writeBuffer(vertexBuffer, 0, triangle.vertex)
        // create color buffer
        const colorBuffer = device.createBuffer({
            label: 'GPUBuffer store rgba color',
            size: 4 * 4, // 4 * float32
            usage: GPUBufferUsage.UNIFORM | GPUBufferUsage.COPY_DST,
        })
        device.queue.writeBuffer(colorBuffer, 0, new Float32Array([1, 1, 0, 1]))

        // create a uniform group for color
        const uniformGroup = device.createBindGroup({
            label: 'Uniform Group with colorBuffer',
            layout: pipeline.getBindGroupLayout(0),
            entries: [
                {
                    binding: 0,
                    resource: {
                        buffer: colorBuffer
                    }
                }
            ]
        })
        // return all vars
    //     console.log('working');
        this.pipelineObj = { pipeline, vertexBuffer, colorBuffer, uniformGroup }
        this.run();
    }
    public run(){
        // const canvas = document.querySelector('canvas')
        // if (!canvas)
        //     throw new Error('No Canvas')
        // const {device, context, format} = await initWebGPU(canvas)
        // const pipelineObj = await initPipeline(device, format)
        
        // first draw
        this.draw(this.device, this.ctx, this.pipelineObj)
    
        // update colorBuffer if color changed
        document.querySelector('input[type="color"]')?.addEventListener('input', (e:Event) => {
            // get hex color string
            const color = (e.target as HTMLInputElement).value
        //     console.log(color)
            // parse hex color into rgb
            const r = +('0x' + color.slice(1, 3)) / 255
            const g = +('0x' + color.slice(3, 5)) / 255
            const b = +('0x' + color.slice(5, 7)) / 255
            // write colorBuffer with new color
            this.device.queue.writeBuffer(this.pipelineObj.colorBuffer, 0, new Float32Array([r, g, b, 1]))
            this.draw(this.device, this.ctx, this.pipelineObj)
        })
        
        // update vertexBuffer
        document.querySelector('input[type="range"]')?.addEventListener('input', (e:Event) => {
            // get input value
            const value = +(e.target as HTMLInputElement).value
        //     console.log(value)
            // chagne vertex 0/3/6
            triangle.vertex[0] = 0 + value
            triangle.vertex[3] = -0.5 + value
            triangle.vertex[6] = 0.5 + value
            // write vertexBuffer with new vertex
            this.device.queue.writeBuffer(this.pipelineObj.vertexBuffer, 0, triangle.vertex)
            this.draw(this.device, this.ctx, this.pipelineObj)
        })
        // re-configure context on resize
        window.addEventListener('resize', ()=>{
            this.canvas.width = this.divHost.clientWidth;
            this.canvas.height = this.divHost.clientHeight;
            // don't need to recall context.configure() after v104
            this.draw(this.device, this.ctx, this.pipelineObj);
        })
    }
    draw(device: GPUDevice, context: GPUCanvasContext, pipelineObj: {
        pipeline: GPURenderPipeline,
        vertexBuffer: GPUBuffer,
        colorBuffer: GPUBuffer,
        uniformGroup: GPUBindGroup
    }) {
        const commandEncoder = device.createCommandEncoder()
        const view = context.getCurrentTexture().createView()
        const renderPassDescriptor: GPURenderPassDescriptor = {
            //@ts-ignore
            colorAttachments: [
                {
                    view: view,
                    clearValue: { r: 0, g: 0, b: 0, a: 1.0 },
                    loadOp: 'clear',
                    storeOp: 'store'
                }
            ]
        }
        const passEncoder = commandEncoder.beginRenderPass(renderPassDescriptor)
        passEncoder.setPipeline(pipelineObj.pipeline)
        // set uniformGroup
        passEncoder.setBindGroup(0, pipelineObj.uniformGroup)
        // set vertex
        passEncoder.setVertexBuffer(0, pipelineObj.vertexBuffer)
        // 3 vertex form a triangle
        passEncoder.draw(triangle.vertexCount)
        passEncoder.end()
        // webgpu run in a separate process, all the commands will be executed after submit
        device.queue.submit([commandEncoder.finish()])
    }
}